import React from 'react';
import { useDispatch } from 'react-redux';
import useStyles from './styles';
import { Paper, Box, Grid, Typography, Divider, IconButton } from '@material-ui/core';
import { ExpandMore, AttachFile } from '@material-ui/icons';
import clsx from 'clsx';

interface IProps {
    photo?: string;
    run_date: string;
    todotask_name: string;
    tasker_name: string;
    content: string;
    attachment?: string;
}

export default function Item(props:IProps) {
    const classes = useStyles();
    const dispatch = useDispatch();
    const {
        photo,
        run_date,
        todotask_name,
        tasker_name,
        content,
        attachment
    } = props;

    return (
        <Paper className={clsx(classes.paper, 'minHeight100')} >
            <Box position="relative">
                <Grid container>
                    <Grid item xs={2} container justify="center" alignItems="center">
                        <Box paddingX="5px">
                            <img
                                className={classes.img}
                                alt="complex"
                                src={photo ? photo : require('@/assets/images/default.jpg')}
                            />
                        </Box>
                    </Grid>
                    <Grid item xs={10} container>
                        <Grid item xs="auto" container direction="column">
                            <Grid item xs>
                                <Typography gutterBottom variant="subtitle1">
                                    {run_date}
                                </Typography>
                                <Typography variant="subtitle2" gutterBottom>
                                    <Box fontSize="12px">
                                        任务名称：{todotask_name}
                                    </Box>
                                    <Box fontSize="12px">
                                        责任人：{tasker_name}
                                    </Box>
                                    <Box fontSize="12px">
                                        任务描述：{content}
                                    </Box>
                                </Typography>
                            </Grid>
                            <Grid item>
                                <Divider />
                                <Typography variant="subtitle2" gutterBottom>
                                    <IconButton
                                        // onClick={() => props.history.push(`/tasksDetail`)}
                                        className={classes.detailBtn}>
                                        <Box
                                            fontSize="12px"
                                        >
                                            明细
                                        </Box>
                                        <ExpandMore color="primary" />
                                    </IconButton>
                                </Typography>
                            </Grid>
                        </Grid>
                    </Grid>
                </Grid>
                <Box
                    position="absolute"
                    right="0%"
                    top="10%"
                >
                    <Choose>
                        <When condition={attachment}>
                            <a href={attachment} download>
                                <IconButton>
                                    <AttachFile color="primary" />
                                </IconButton>
                            </a>
                        </When>
                        <Otherwise>
                            <IconButton onClick={() => dispatch({
                                type: 'SHOW_MESSAGE',
                                payload: {
                                    open: true,
                                    variant: 'warning',
                                    message: '无可用附件下载'
                                }
                            })}>
                                <AttachFile color="primary" />
                            </IconButton>
                        </Otherwise>
                    </Choose>
                </Box>
            </Box>
        </Paper>
    );
}