import React, { useState, useEffect } from 'react';
import { Modal, Box, Paper, Divider, Grid, Zoom } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import clsx from 'clsx';
import DoneState from './done-state';

interface IDetail extends IndexMap<string | number> {
    id:  number | string;
    activity_done_day: string | number;
    activity_task: string;
    done_state: string | number;
    done_state_label: string;
    drafttype: string;
    drug_name: string;
    eartag: string;
    event_date: string;
    pen_name: string;
    rem: string;
    technician: string;
    workflow_type_name: string;
}
interface IProps {
    open: boolean;
    setOpen: (open: boolean) => void;
    detail: IDetail;
}
const DetailMap: IndexMap<string> = {
    activity_done_day: '完成期限',
    activity_task: '任务名称',
    done_state_label: '状态',
    drafttype: '流程类型',
    drug_name: '药品',
    eartag: '耳号',
    event_date: '任务日期',
    pen_name: '牛舍',
    rem: '备注',
    technician: '技术员',
    workflow_type_name: '工作流程'
};
const DetailFields = [
    'activity_task',
    'drafttype',
    'workflow_type_name',
    'eartag',
    'pen_name',
    'drug_name',
    'technician',
    'event_date',
    'activity_done_day',
    'rem'
];

const useStyles = makeStyles((theme: any) => ({
    content: {
        position: 'absolute',
        top: '50%',
        left: '50%',
        width: '80%',
        transform: 'translate3d(-50%,-50%, 0)',
        backgroundColor: '#fff',
        borderRadius: 5,
        padding: '0 10px 10px',
        lineHeight: 2
    },
    label: {
        color: '#333',
        marginRight: 5
    },
    value: {
        color: theme.palette.primary.main
    },
    donestate: {
        top: '50%',
        left: '50%',
        position: 'absolute',
        transform: 'translate3d(-50%, -50%, 0)',
    }
}))
export default function Detail(props: IProps) {
    const classes = useStyles();
    const [doneStateShow, setDoneStateShow] = useState(false);
    const { open, setOpen, detail } = props;

    useEffect(() =>{
        let timeId: any;
        timeId = setTimeout(() =>{
            setDoneStateShow(open);
        }, 200);
        return () => timeId && clearTimeout(timeId);
    }, [open]);

    return (
        <Zoom in={doneStateShow}>
            <Modal
                aria-labelledby="simple-modal-title"
                aria-describedby="simple-modal-description"
                open={open}
                keepMounted
                onClose={() => setOpen(false)}
            >
                <Paper className={classes.content}>
                    <Box className="text-center" fontSize="1.2rem">明细</Box>
                    {
                        DetailFields.map(item => (
                            <Box key={item}>
                                <Divider color="primary" />
                                <Grid container>
                                    <Grid item xs="auto">
                                        <div className={clsx(classes.label, 'text-left')}>
                                            {DetailMap[item]}:
                                        </div>
                                    </Grid>
                                    <Grid item xs>
                                        <div className={clsx(classes.value, 'text-right')}>
                                            {detail[item]}
                                        </div>
                                    </Grid>
                                </Grid> 
                            </Box>
                        ))
                    }
                    <Box className={classes.donestate}>
                        {/* <If condition={doneStateShow}>
                            <Zoom in={doneStateShow}>
                                <DoneState
                                    down_state={detail.done_state}
                                    down_state_label={detail.done_state_label}
                                />
                            </Zoom>
                        </If> */}
                        <DoneState
                                    down_state={detail.done_state}
                                    down_state_label={detail.done_state_label}
                                />
                    </Box>
                </Paper>
            </Modal>
        </Zoom>
    )
}