import React, { useState, useEffect, useCallback } from 'react';
import { RouteComponentProps } from 'react-router-dom';
// import 'react-circular-progressbar/dist/styles.css';
// import { ListView } from 'antd-mobile';
import { Box, Container } from '@material-ui/core';
import { Subscription } from 'rxjs';
import { map } from 'rxjs/operators';
import dayjs from 'dayjs';
import SwipeableViews from 'react-swipeable-views';
import clsx from 'clsx';
import { useTheme } from '@material-ui/core/styles';
import { fetchTodoTaskRuns$, getDraftsHz$ } from '@/api';
import AppBar from '@/components/app-bar';
import { TabPanel, Tabs, Item, Not3Item } from './components';
import useStyles from './styles';
import ClickLoadMore from '@/components/click-load-more';

const Tasks: React.FC<RouteComponentProps> = props => {
    const classes = useStyles();
    const theme = useTheme();
    const tabList = ['繁殖', '保健', '营养', '任务'];
    const [listMap, setListMap] = useState<IndexMap<any[]>>({
        0: [],
        1: [],
        2: [],
        3: []
    });
    const [pageMap, setPageMap] = useState<IndexMap<number>>({
        0: 1,
        1: 1,
        2: 1,
        3: 1,
    });
    const [nextMap, setNextMap] = useState<IndexMap<string>>({
        0: 'init',
        1: 'init',
        2: 'init',
        3: 'init',
    });
    const [firstFetchData, setFirstFetchData] = useState<IndexMap<boolean>>({
        0: true,
        1: true,
        2: true,
        3: true,
    });
    const [loading, setLoading] = useState(false);
    const [tabValue, setTabValue] = useState(3);
    const page_size = 5;

    function handleChange(event: React.ChangeEvent<{}>, newValue: number) {
        setTabValue(newValue);
    }

    const getTask3List = useCallback(() => {
        const ApiInst = fetchTodoTaskRuns$({ page: pageMap[3], page_size }).pipe(
            map(({response}) => response),
            map(response => [response.results, response.next]),
            map(([list, next]) => {
                const tasksList =  list.map((item: any) => {
                    return {
                        ...item,
                        toNowDate: dayjs(item.run_date).diff(dayjs(), 'day')
                    }
                });
                return [tasksList, next];
            })
        ).subscribe(
            (data: any) => {
                console.log('data', data);
                setLoading(false);
                setListMap({ ...listMap, 3: listMap[3].concat(data[0]) });
                setNextMap({ ...nextMap, 3: data[1] });
                firstFetchData[3] && setFirstFetchData({...firstFetchData, 3: false});
            },
            () => setLoading(false)
        );
        return ApiInst;
    }, [pageMap]);
    const getTaskNot3List = useCallback(() => {
        const UsageId: IndexMap<string> = {
            0: '270',
            1: '269',
            2: '268'
        };
        const Params = {
            page: pageMap[tabValue],
            page_size,
            usage_id: UsageId[String(tabValue)]
        };
        const ApiInst = getDraftsHz$(Params).pipe(
            map(({response}) => response),
            map(response => Array.isArray(response) ? [response, null] : [response.results, response.next]),
        ).subscribe(
            (data: any) => {
                console.log('data', data);
                setLoading(false);
                setListMap({ ...listMap, [tabValue]: listMap[tabValue].concat(data[0]) });
                setNextMap({ ...nextMap, [tabValue]: data[1] });
                firstFetchData[tabValue] && setFirstFetchData({...firstFetchData, [tabValue]: false});
            },
            () => setLoading(false)
        );
        return ApiInst;
    }, [tabValue, pageMap]);

    const loadMore = (index: number) => () => {
        if (nextMap[index]) {
            setPageMap({
                ...pageMap,
                [index]: pageMap[index] + 1
            });
        }
    };

    useEffect(() => {
        let ApiInst: Subscription;
        if (pageMap[tabValue]!== 1) {
            setLoading(true);
            ApiInst = tabValue !== 3 ? getTaskNot3List() : getTask3List();
        }
        return () =>  ApiInst && ApiInst.unsubscribe();
    }, [pageMap]);

    useEffect(() => {
        let ApiInst: Subscription;
        if (firstFetchData[tabValue]) {
            setLoading(true);
            ApiInst = tabValue !== 3 ? getTaskNot3List() : getTask3List();
        }
        return () =>  ApiInst && ApiInst.unsubscribe();
    }, [firstFetchData, tabValue, getTaskNot3List, getTask3List]);

    return (
        <Box className={clsx('bg-gray')}>
            <AppBar title="任务清单" />
            <Tabs value={tabValue} onChange={handleChange} tabList={tabList} />
            <Container>
                <SwipeableViews
                    axis={theme.direction === 'rtl' ? 'x-reverse' : 'x'}
                    index={tabValue}
                    onChangeIndex={(index) => setTabValue(index)}
                >
                    {
                        tabList.map((item, index) => {
                            return (
                                <TabPanel
                                    key={item}
                                    value={tabValue}
                                    index={index}
                                    dir={theme.direction}
                                >
                                    <Box className={classes.content}>
                                        <Choose>
                                            <When condition={index !== 3}>
                                                {
                                                    listMap[index].map((item: any, i) => (
                                                        <Not3Item 
                                                            key={`tasks-${index}item-${i}`}
                                                            {...item}
                                                            onClick={() => props.history.push('/tasksDetail')}
                                                        />
                                                    ))
                                                }
                                            </When>
                                            <Otherwise>
                                                {
                                                    listMap[3].map((item: any, i) => (
                                                        <Item
                                                            key={`tasks-3-item-${i}`}
                                                            {...item}
                                                        />
                                                    ))
                                                }
                                            </Otherwise>
                                        </Choose>
                                        <ClickLoadMore
                                            loading={loading}
                                            loadMore={loadMore(index)}
                                            hasMore={Boolean(nextMap[index])}
                                        />
                                    </Box>
                                </TabPanel>
                            )
                        })
                    }
                </SwipeableViews>
            </Container>
        </Box>
    );
}
export default Tasks;