import React, { useState, useEffect } from 'react';
import { RouteComponentProps } from 'react-router-dom';
import {
    Box,
    Grid,
    Divider,
    Table,
    TableHead,
    TableBody,
    TableCell,
    TableRow
} from '@material-ui/core';
import Rating from '@material-ui/lab/Rating';
import clsx from 'clsx';
import { CircularProgressbar, buildStyles } from 'react-circular-progressbar';
import 'react-circular-progressbar/dist/styles.css';
import AppBar from '@/components/app-bar';
import { getDrafts$ } from '@/api';
import { map, pluck, finalize } from 'rxjs/operators';
import useStyles from './styles';
import Detail from './components/detail';
import ClickLoadMore from '@/components/click-load-more';

interface Detail extends IndexMap<string | number> {
    id: number | string;
    activity_done_day: string | number;
    activity_task: string;
    done_state: string | number;
    done_state_label: string;
    drafttype: string;
    drug_name: string;
    eartag: string;
    event_date: string;
    pen_name: string;
    rem: string;
    technician: string;
    workflow_type_name: string;
}

const TasksDetail: React.FC<RouteComponentProps<{id: string}>> = props => {
    const classes = useStyles();
    const [open, setOpen] = useState(false);
    const [loading, setLoading] = useState(false);
    const [next, setNext] = React.useState(null);
    const [List, setList] = useState<Detail[]>([]);
    const [page, setPage] = useState(1);
    const [activeData, setActiveData] = useState<Detail>({
        id: '',
        activity_done_day: '',
        activity_task: '',
        done_state: '',
        done_state_label: '',
        drafttype: '',
        drug_name: '',
        eartag: '',
        event_date: '',
        pen_name: '',
        rem: '',
        technician: '',
        workflow_type_name: '',
    });

    const handleClick = (target: string, index: number) => {
        const actRet = List[index];
        if (target === 'cow') {
            props.history.push(`/cowDetail/${actRet.id}?eartag=${actRet.eartag}`);
        } else {
            setActiveData(actRet);
            setOpen(true);
        }
    };
    const loadMore = () => {
        if (next) {
            setPage(page + 1);
        }
    };
    useEffect(() => {
        // const id = props.match.params.id;
        setLoading(true);
        const ApiInst = getDrafts$({ page, page_size: 5}).pipe(
            pluck('response'),
            map(response => {
                const DoneStateLabel: IndexMap<string> = {
                    0: '待完工',
                    1: '已完成',
                    2: '已过期'
                };
                const formatDetail = (item: Detail) => {
                    const ret: IndexMap<string | number> = {};
                    for(const [key, value] of Object.entries(item)){
                        ret[key] = value || '暂无';
                    }
                    ret.done_state_label = DoneStateLabel[item.done_state];
                    ret.done_state = item.done_state;
                    return ret;
                }
                const List = response.results.map(formatDetail);
                return [List, response.next];
            }),
            finalize(() => setLoading(false))
        ).subscribe(
            (data: any) => {
                // console.log('data', data);
                setList(List.concat(data[0]));
                setNext(data[1])
            },
            error => {
                console.log('error', error);
            }
        );
        return () =>  ApiInst.unsubscribe();
    }, [page]);

    return (
        <Box paddingBottom="30px" className={classes.root}>
            <AppBar title="完工单明细" />
            {/* <Grid container alignItems="center">
                <Grid item xs>
                    <Box component="fieldset" mb={3} borderColor="transparent">
                        <Box fontSize={12}>任务 100， 完成 75</Box>
                        <Rating name="half-rating" value={75 * 5 / 100} readOnly />
                    </Box>
                </Grid>
                <Grid item  xs="auto">
                    <CircularProgressbar
                        className={classes.circularProgressba}
                        value={75}
                        text='75%'
                        styles={buildStyles({
                            pathColor: `rgba(207, 0, 15, ${75 / 100})`,
                            textColor: `rgba(207, 0, 15, ${75 / 100})`
                        })}
                    />
                </Grid>
            </Grid> */}
            <Divider />
            <Table className={classes.table}>
                <TableHead>
                    <TableRow>
                        <TableCell align="center" className={clsx(classes.tablecell, classes.tableHeader)}>牛号</TableCell>
                        <TableCell align="center" className={clsx(classes.tablecell, classes.tableHeader)}>类型</TableCell>
                        <TableCell align="center" className={clsx(classes.tablecell, classes.tableHeader)}>任务日期</TableCell>
                        <TableCell align="center" className={clsx(classes.tablecell, classes.tableHeader)}>任务状态</TableCell>
                    </TableRow>
                </TableHead>
                <TableBody>
                    {List.map((row: any, index: number) => (
                        <TableRow hover key={`${row.eartag}-${index}`}>
                            <TableCell onClick={() => handleClick('cow', index)} align="center" className={classes.tablecell} >{row.eartag}</TableCell>
                            <TableCell onClick={() => handleClick('detail', index)} align="center" className={classes.tablecell}>{row.drafttype}</TableCell>
                            <TableCell onClick={() => handleClick('detail', index)} align="center" className={classes.tablecell}>{row.event_date}</TableCell>
                            <TableCell onClick={() => handleClick('detail', index)} align="center" className={classes.tablecell}>{row.done_state_label}</TableCell>
                        </TableRow>
                    ))}
                </TableBody>
            </Table>
            <ClickLoadMore loadMore={loadMore} loading={loading} hasMore={Boolean(next)} />
            <Detail open={open} detail={activeData} setOpen={setOpen} />
        </Box>
    )
}
export default TasksDetail;