import React from 'react';
import { Box } from '@material-ui/core';
import { amber, green } from '@material-ui/core/colors';
import { makeStyles } from '@material-ui/core/styles';
import clsx from 'clsx';

interface IProps {
    down_state_label: string;
    down_state: string | number;
}
const useStyles = makeStyles((theme: any) => ({
    success: {
        backgroundColor: green[600],
    },
    error: {
        backgroundColor: theme.palette.error.dark,
    },
    info: {
        backgroundColor: theme.palette.primary.main,
    },
    warning: {
        backgroundColor: amber[700],
    },
    root: {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        height: 80,
        width: 80,
        borderRadius: '50%',
        transform: 'rotate(-20deg)',
        opacity: 0.7,
        color: '#fff',
        fontSize: 12,
        fontWeight: 'bolder',
        boxShadow: '0 2px 5px #ccc'
    },
}));

const DoneState: React.FC<IProps> = (props: IProps) => {
    const { down_state_label, down_state } = props;
    const classes = useStyles();
    const BgColor: IndexMap<string> = {
        0: classes.warning,
        1: classes.success,
        2: classes.error
    }
    return (
        <Box
            className={clsx(classes.root, BgColor[down_state])}
        >
            {down_state_label}
        </Box>
    )
};
export default DoneState;